/////////////////////////////
import { webSockets } from "../gateway/index";
import { connectStatus, WebSockets } from "../gateway/WebSockets";

export class Deno {
  constructor() {}
  // 调用deno的函数
  async callFunction(handleFn: string) {
    console.log(JSON.stringify(webSockets));
    const status = webSockets.socketChange();
    console.log(status);
    if (status === connectStatus.已关闭连接) {
      return;
    }
    webSockets.sendData(handleFn);
  }
}
